        -ms-flex-preferred-size: 0;
        flex-basis: 0;
        -ms-flex-positive: 1;
        flex-grow: 1;
        max-width: 100%;
    }
    .xs {
        &-visible {
            display: block;
        }
        &-hidden {
            display: none
        }
