*,
*::before,
*::after {
    box-sizing: border-box; // 1
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    input:-internal-autofill-selected {
        background: none !important;
    }
}

html {
    box-sizing: border-box;
    @include respond-below (sm) {
        font-size: 14px;
    }
    @include respond-above (sm) {
        font-size: 15px;
    }
    @include respond-above (lg) {
        font-size: 16px;
    }
}

body {
    font-size: 1rem;
    font-weight: 400;
    font-family: $base-font-family;
    color: $gray-700;
 
}

ul li {
    list-style: none;
    margin: 0;
    padding: 0;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none;
    &:focus {
        outline: none;
    }
}

small,
.small {
    font-size: 80% !important;
    font-weight: 400 !important;
}

.medium {
    font-size: 90% !important;
    font-weight: 400 !important;
}
.large {
    font-size: 95% !important;
    font-weight: 400 !important;
}
mark,
.mark {
    padding: 5px;
    background: rgba($warning, 0.15);
}

button {
    background: none;
    border: none;
    -webkit-appearance: none;
    cursor: pointer;
    outline: none;
}

 ::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: $gray-300;
    font-weight: $font-weight-light;
}

 :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: $gray-300;
    font-weight: $font-weight-light;
}

 ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: $gray-300;
    font-weight: $font-weight-light;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

.h-auto {
    width: 100% !important;
}

.w-auto {
    width: auto !important;
}

.mh-100 {
    max-height: 100%;
}

.mw-100 {
    max-height: 100%;
}

.min {
    &-vw {
        min-width: 100vw;
    }
    &-vh {
        min-height: 100vh;
    }
}

.vw-100 {
    width: 100vw;
}

.vh {
    height: 100vh;
}

.c-pointer,
.pointer {
    cursor: pointer;
}