table {
    width: 100%;
    background: transparent;
    thead {
        th {
            vertical-align: bottom;
            border-bottom: 2px solid $gray-300;
            color: $gray-800;
            padding: 15px;
        }
    }
    td,
    th {
        vertical-align: top;
        border-bottom: 1px solid $gray-300;
        color: $gray-600;
        padding: 15px;
        text-align: left;
        @include transition(0.3s ease);
    }
    &.bordered {
        thead {
            th {
                border: 1px solid $gray-300;
            }
        }
        td,
        th {
            border: 1px solid $gray-300;
        }
    }
    &.hoverable {
        tbody tr {
            &.is-active {
                background: rgba($primary, 0.16);
            }
            &:not(.is-active) {
                &:hover {
                    background: $gray-200;
                }
            }
        }
    }
    &.alternates {
        tbody tr {
            &.is-active {
                background: rgba($primary, 0.05);
            }
            &:not(.is-active):nth-child(even) {
                background: $gray-100;
            }
        }
    }
    &.data-grid {
        thead th {
            background: $gray-200;
            color: $gray-700;
            font-weight: $font-weight-medium;
            border: none;
        }
        tbody tr {
            &:not(:last-child) {
                border-bottom: 1px solid rgba($gray-900, 0.05);
            }
            &:nth-child(even) {
                background: rgba($primary, 0.05);
            }
            &:hover {
                background: rgba($primary, 0.085);
            }
        }
    }
}

.table-responsive {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
}