.no-spc {
    padding-left: 0;
    padding-right: 0;
}

.table {
    width: 100%;
    display: table;
}

.underline {
    text-decoration: underline;
}

.breadcrumb {
    span {
        +span {
            padding: 0 5px;
            &:before {
                display: inline-block;
                font-family: FontAwesome;
                font-size: inherit;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                content: '\f054';
                margin-right: 8px;
                font-size: 10px;
                color: #bbb;
            }
        }
    }
}

footer {
    display: none;
}

.bg-alternates {
    &:nth-child(even) {
        background: $gray-100;
    }
}

.d {
    &-i-block {
        display: inline-block !important;
    }
    &-block {
        display: block !important
    }
    &-t-row {
        display: table-row;
    }
    &-t-cell {
        display: table-cell;
    }
}

.t {
    &-left {
        text-align: left;
    }
    &-center {
        text-align: center;
    }
    &-right {
        text-align: right;
    }
    &-truncate {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}

.d {
    &-block {
        display: block !important
    }
    &-i-block {
        display: inline-block !important;
    }
    &-none {
        display: none !important;
    }
}

.t {
    &-nowrap {
        white-space: nowrap;
    }
}

.hidden {
    display: none;
}

.bg-none {
    background: none !important;
}

.b-s-none {
    box-shadow: none !important;
}

.br-none {
    border: none !important;
}

.br-r-none {
    border-radius: 0 !important;
}

.f {
    &-left {
        float: left;
    }
    &-right {
        float: right;
    }
    &-none {
        float: none;
    }
}

.c {
    &-left {
        clear: left;
    }
    &-right {
        clear: right;
    }
    &-both {
        clear: both;
    }
}

.clearfix {
    @include clearfix();
}

.flex {
    @include flexbox();
}

.i-flex {
    @include inline-flex();
}

.f {
    &-wrap {
        @include flex-wrap(wrap);
    }
    &-nowrap {
        @include flex-wrap(nowrap) ;
    }
    &-inline {
        @include inline-flex();
    }
    &-start {
        @include flexbox();
        @include justify-content(flex-start);
        text-align: left
    }
    &-center {
        @include flexbox();
        @include justify-content(center);
        text-align: center
    }
    &-end {
        @include flexbox();
        @include justify-content(flex-end);
        text-align: right
    }
    &-s-between {
        @include justify-content(space-between);
    }
    &-s-around {
        @include justify-content(space-around);
    }
    &-top {
        @include flexbox();
        @include align-items(flex-start);
    }
    &-middle {
        @include flexbox();
        @include align-items(center);
    }
    &-bottom {
        @include flexbox();
        @include align-items(flex-end);
    }
    &-baseline {
        @include flexbox();
        @include align-items(baseline);
    }
    &-stretch {
        @include flexbox();
        @include align-items(stretch);
    }
    &-a-s-end {
        @include flexbox();
        @include align-self(flex-end);
    }
    &-a-s-start {
        @include flexbox();
        @include align-self(flex-start);
    }
    &-a-s-bottom {
        @include flexbox();
        @include align-self(baseline);
    }
    &-a-s-center {
        @include flexbox();
        @include align-self(center);
    }
    &-a-s-stretch {
        @include flexbox();
        @include align-self(stretch);
    }
    &-grow-0 {
        @include flex-grow(0);
    }
    &-grow-1 {
        @include flex-grow(1);
    }
    &-shrink-1 {
        @include flex-shrink(1);
    }
    &-shrink-0 {
        @include flex-shrink(0);
    }
    &-row {
        @include flex-direction(row);
    }
    &-column {
        @include flex-direction(column);
    }
}

.p {
    &-relative {
        position: relative;
    }
    &-absolute {
        position: absolute;
        &.l-0 {
            left: 0;
        }
        &.r-0 {
            right: 0;
        }
        &.t-0 {
            top: 0;
        }
        &.b-0 {
            bottom: 0;
        }
        
    }
    &-static {
        position: static;
    }
    &-fixed {
        position: fixed;
    }
    &-l-0 {
        left: 0 !important;
    }
    &-r-0 {
        right: 0  !important;
    }
    &-t-0 {
        top: 0  !important;
    }
    &-b-0 {
        bottom: 0  !important;
    }
    &-l-auto{
        left:auto  !important;
    }
    &-r-auto{
        right:auto  !important;
    }
    &-t-auto{
        top:auto  !important;
    }
    &-b-auto{
        bottom:auto  !important;
    }
}

.o {
    &-visible {
        overflow: visible !important;
    }
    &-hidden {
        overflow: hidden !important;
    }
    &-auto {
        overflow: auto !important;
    }
}

.sfButtonwrapper {
    .btn {
        &:last-child {
            margin: 0;
        }
    }
}

.f-fill {
    flex: 1 1 auto !important;
}

.f-g-0 {
    flex-grow: 0 !important;
}

.f-s-0 {
    flex-shrink: 0 !important;
}

.f-g-1 {
    flex-grow: 1 !important;
}

.f-s-1 {
    flex-shrink: 1 !important;
}

.fas,
.far {
    font-family: "Font Awesome 5 Free"
}

::-webkit-scrollbar {
    width: 5px;
}


/* Track */

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-border-radius: 5px;
    border-radius: 5px;
}


/* Handle */

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(0, 0, 0, 0.2);
}