ul {
    counter-reset: listStyle;
    list-style: none;
}

ul li::before {
    margin-right: 5px;
    display: inline-block;
}

ul.circle li::before {
    content: counter(listStyle, circle);
}

ul.disc li::before {
    content: counter(listStyle, disc);
}

ul.square li::before {
    content: counter(listStyle, square);
}

ol {
    counter-reset: listStyle;
    list-style: none;
}

ol li {
    margin-left: 1em;
    counter-increment: listStyle;
}

ol li::before {
    margin-right: 5px;
    display: inline-block;
}

ol.number li::before {
    content: counter(listStyle)'.';
}

ol.alphaSmall li::before {
    content: counter(listStyle, lower-alpha)'.';
}

ol.alphaCaps li::before {
    content: counter(listStyle, upper-alpha)'.';
}

ol.romanSmall li::before {
    content: counter(listStyle, lower-roman)'.';
}

ol.romanCaps li::before {
    content: counter(listStyle, upper-roman)'.';
}

