.tab {
    .card {
        box-shadow: 0 0 5px rgba(black, 0.05);
        border-radius: 6px;
        background: none;
        border: 1px solid $gray-200;
        padding: 0;
        box-shadow: 0 0 0 rgba(0, 0, 0, 0.05);
        @include respond-above(sm) {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            border: none;
            box-shadow: none;
        }
        h5 {
            >.fa {
                display: none;
                font-size: 1rem;
            }
            @include respond-below(sm) {
                display: flex;
                justify-content: space-between;
                align-items: center;
                >.fa {
                    display: block;
                }
            }
        }
        .card-header {
            background: rgba($gray-200, 0.5);
            color: $gray-400;
            cursor: pointer;
            border: none;
            padding: 20px;
            @include respond-below(sm) {
                &:not(.active) {
                    border-bottom: 1px solid $gray-200;
                }
                &:not(:first-of-type) {
                    border-bottom: 1px solid $gray-200;
                }
            }
            @include transition (0.3s ease);
            @include respond-above(sm) {
                background: none;
                -webkit-box-ordinal-group: 2;
                order: 1;
                padding: 15px 25px;
                display: block;
                font-weight: $font-weight-medium;
                border-bottom: 3px solid transparent;
            }
            h5,
            a {
                color: inherit;
            }
            &:hover {
                color: $primary;
                background: $gray-200;
                @include respond-above(sm) {
                    background: none;
                }
            }
            &.active {
                @include respond-above(sm) {
                    border-color: $primary;
                }
                color: $primary;
            }
        }
        .card-body {
            background: #fff;
            padding: 15px 25px;
            @include respond-above(sm) {
                box-shadow: 0 2px 20px rgba(black, 0.1);
                border-radius: 0 0 5px 5px;
            }
        }
    }
    .collapse {
        display: none;
        -webkit-box-ordinal-group: 100;
        order: 99;
        -webkit-box-flex: 1;
        flex-grow: 1;
        width: 100%;
        background: #fff;
        &.show {
            display: block
        }
    }
    li>a {
        padding: 15px 25px;
        display: block;
        font-weight: $font-weight-medium;
        color: $gray-500;
        border-bottom: 1px solid transparent;
        @include transition(0.3s ease);
        &.active {
            color: $primary;
            border-color: $primary;
        }
    }
    &-pane {
        background: #fff;
        box-shadow: 0 2px 20px rgba(black, 0.1);
        border-radius: 0 0 5px 5px;
        padding: 15px 25px;
        display: none;
        &.active {
            display: block;
        }
    }
    @include respond-above(md){
    &.vertical{
        .card{
            border-radius:0;
            position:relative;
            display:flex;
            flex-direction:column;
            &-header{
                background:#fff;
                width:25%;
                border-radius:0;
                &.active{
                    border:none;
                    background:$primary;
                    color:#fff;
                }
                
            }
            height:100%;
        }
        .collapse{
            background:#fff;
            height:100%;
            overflow:auto;
            overflow-x:hidden;
            position: absolute;
            left: 25%;
            width: calc(75% - 40px);
            margin-left:35px;
            padding:25px 35px;
        }
    }
    }
   
    
}

.accordion {
    .card {
        &:not(:first-of-type) {
            border-top-left-radius: 0;
            border-top-right-radius: 0;
        }
        &:not(:last-of-type) {
            border-bottom: 0;
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }
    }
    .card-header {
        color: $gray-600;
        cursor: pointer;
        @include transition (0.3s ease);
        &:not(.active) {
            border-bottom: 1px solid $gray-200;
        }
        &:not(:first-of-type) {
            border-bottom: 1px solid $gray-200;
        }
        &:hover {
            color: $primary;
            background: $gray-200;
        }
        >*,
        a {
            color: inherit;
        }
        &.active {
            color: $primary;
            background: rgba($gray-300, 0.5);
            >*,
            a {
                color: inherit;
            }
        }
    }
    h5,
    .head {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .collapse {
        display: none;
        &.show {
            display: block
        }
    }
}

.card {
    box-shadow: 0 0 5px rgba(black, 0.05);
    border-radius: 6px;
    background: #fff;
    border: 1px solid $gray-200;
    overflow: hidden;
    &.none {
        border: none;
        box-shadow: none
    }
    .card {
        &-body,
        &-footer,
        &-header {
            padding: 20px;
        }
        &-header {
            background: $gray-100;
            border-bottom: 1px solid $gray-200;
            i {
                transition: 0.3s transform ease;
            }
            &.active {
                i {
                    transform: rotate(-180deg);
                }
            }
        }
        &-body {
            h5 {
                margin-bottom: 15px;
                a {
                    color: inherit;
                    &:hover {
                        color: $primary;
                    }
                }
            }
            p {
                color: $gray-900;
                margin-bottom: 20px;
                &:last-child {
                    margin: 0;
                }
            }
        }
        .card-footer {
            background: $gray-200;
            p {
                &:last-child {
                    margin: 0
                }
            }
        }
    }
}

.list-group {
    display: flex;
    flex-direction: column;
    padding: 0;
    &-hz {
        flex-direction: row;
        .group-item {
            &:first-child {
                border-bottom-left-radius: 8px;
                border-top-right-radius: 0px;
            }
            &:last-child {
                border-top-right-radius: 8px;
                border-bottom-left-radius: 0px;
            }
            &:not(:last-child) {
                border-bottom: none;
            }
            +.group-item {
                border-top-width: 1px;
                border-left-width: 0;
            }
        }
    }
}

.group-item {
    color: $gray-800;
    // border: 1px solid $gray-400;
    border: 1px solid #e4eaf2;
    padding: 10px 20px;
    background: #fff;
    @include transition (0.3s ease);
    +.group-item {
        border-top-width: 0;
    }
    &:first-child {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
    }
    &:last-child {
        border-bottom-right-radius: 8px;
        border-bottom-left-radius: 8px;
    }
    &.group-item-link {
        cursor: pointer;
        &:hover {
            color: lighten( $gray-100, 15%);
        }
        &.active {
            color: $primary;
            background: #fff;
            
        }
        &-bg {
            cursor: pointer;
            &:hover {
                background: $gray-100;
            }
            &.active {
                background: $primary;
                color: #fff;
                >a{
                    color:inherit;
                }
            }
        }
    }
    &.group-item-bl-y {
        border-left: none;
        border-right: none;
        border-radius: 0;
        &:first-child {
            border-top: none;
        }
        &:last-child {
            border-bottom: none;
        }
    }
    &-auto {
        flex: 1 1 auto;
    }
}

//accordion
.card {
    .card-body .accordion {
        h5 {
            margin: 0;
            font-weight: 500;
            font-size: 1.05rem;
        }
    }
}

.accordion {
    .card {
        input[type="checkbox"] {
            &:checked+label:before {
                background: $primary;
                border-color: $primary;
            }
            +label:before,
            .form-input+label:before {
                border: 1px solid $gray-300;
                background: #fff;
            }
        }
        &:not(:first-of-type) {
            border-top-left-radius: 0;
            border-top-right-radius: 0;
        }
        &:not(:last-of-type) {
            border-bottom: 0;
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }
    }
    .card-header {
        color: $gray-600;
        cursor: pointer;
        @include transition (0.3s ease);
        &:hover {
            color: $primary;
            background: $gray-200;
        }
        >*,
        a {
            color: inherit;
        }
        &.active {
            color: $primary;
            background: rgba($gray-300, 0.5);
            >*,
            a {
                color: inherit;
            }
        }
    }
    h5 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        i {
            font-size: 0.85rem;
        }
    }
    .collapse {
        display: none;
        &.show {
            display: block
        }
    }
    input[type="checkbox"]+label:before,
    .form-input+label:before {
        background: $gray-400;
    }
}

.tooltip {
    position: relative;
    cursor: pointer;
    display: inline-block;
    &:before {
        width: 0;
        height: 0;
        border: 5px solid transparent;
        opacity: 0;
        pointer-events: none;
        transition: all .18s ease-out .18s;
        content: "";
        position: absolute;
        z-index: 10;
    }
    &:after {
        opacity: 0;
        pointer-events: none;
        transition: all .18s ease-out .18s;
        text-indent: 0;
        font-size: 0.8rem;
        line-height:1.5;
        font-weight: normal;
        font-style: normal;
        text-shadow: none;
        background: $gray-800;
        border-radius: 2px;
        color: #fff;
        content: attr(data-title);
        font-family: $base-font-family;
        text-transform: none;
        padding: 5px;
        position: absolute;
        white-space: nowrap;
        z-index: 10;
    }
    &:hover,
    &:hover:before,
    &:hover:after {
        opacity: 1;
    }
    &.bottom {
        &:before {
            left: 50%;
            transform: translateX(-50%);
            top: 100%;
            border-bottom-color: $gray-900;
        }
        &:after {
            left: 50%;
            transform: translateX(-50%);
            margin-top: 10px;
            top: 100%;
        }
    }
    &.top {
        &:before {
            left: 50%;
            transform: translateX(-50%);
            bottom: 100%;
            border-top-color: #212529;
        }
        &:after {
            left: 50%;
            transform: translateX(-50%);
            bottom: 100%;
            margin-bottom: 10px;
        }
    }
    &.left {
        &:before {
            top: 50%;
            transform: translateY(-50%);
            right: 100%;
            border-left-color: #212529;
        }
        &:after {
            right: 100%;
            transform: translateY(-50%);
            top: 50%;
            margin-right: 10px;
        }
    }
    &.right {
        &:before {
            top: 50%;
            transform: translateY(-50%);
            left: 100%;
            border-right-color: #212529;
        }
        &:after {
            left: 100%;
            transform: translateY(-50%);
            top: 50%;
            margin-left: 10px;
        }
    }
}
.custom-tooltip{
    position:relative;
    width:100%;
    >span{
        cursor:pointer;
    }
    .tooltip-text{ 
        opacity:0;
        visibility:hidden;
        position: absolute;
        background: #343a40;
        border-radius: 2px;
        color: #fff;
        font-size:0.8rem;
        font-family: $base-font-family;
        padding:5px;
        text-transform: none;
        text-indent: 0; 
        font-weight: normal;
        font-style: normal; 
        text-shadow: none;
        z-index: 10;
        width:100%;
        transition: all .18s ease-out .18s;
        &.top{
            bottom:110%;
            left: 50%;
            transform: translateX(-50%);
        }
        &.left{
            right:100%;
        }
        &.right{
            left:auto;
        }
        &.bottom{
            top: 110%;
            left: 50%;
            transform: translateX(-50%);
        }
    }
   >span{
       &:hover{
           + .tooltip-text{
           visibility:visible;
           opacity:1;
        }
       }
   }
}

//error messages
.act-message {
    border: 1px solid transparent;
    padding: 15px;
    width: 100%;
    border-radius: 3px;
    color: #fff;
    margin-bottom: 35px;
    z-index:1;
    position:relative;
}

.ui-dialog {
    box-shadow: 0 1px 15px rgba(0, 0, 0, 0.15);
    &:focus {
        outline: none;
    }
    .ui-widget-header {
        display: flex;
        width: 100%;
        align-items: center;
        background: $primary;
        color: #fff;
        font-size: 1.25rem;
        padding: 15px 20px;
        >i {
            display: none;
        }
        button {
            margin-left: auto;
            color: #fff;
            font-size: 1rem;
            .ui-icon-closethick {
                &:before {
                    content: '\f00d';
                    font-family: fontAwesome;
                }
                &:hover {
                    color: $gray-200;
                }
            }
        }
    }
    &.ui-widget {
        width: 450px !important;
        position: absolute !important;
        top: 50% !important;
        left: 50% !important;
        transform: translate(-50%, -50%);
        background: #fff;
        margin-bottom: 10px;
        z-index: 10;
        &-content {
            border: none;
        }
    }
    .ui-widget-content {
        border: none;
    }
    button {
        &.ui-button span {
            &:nth-child(2) {
                display: none;
            }
        }
    }
    .ui-dialog-content {
        min-height: 100px !important;
        padding: 20px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        font-size: 1.05rem;
        color: $gray-900;
    }
    .ui-dialog-buttonset {
        padding: 20px;
        text-align: center;
        button {
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
    }
}

.ui-widget-overlay {
    background: rgba(black, 0.65);
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.modal-dialog {
    &.primary {
        .modal-header {
            background: $primary;
            color: #fff;
            button {
                color: #fff;
            }
        }
    }
}

