.sfCheckbox {
    display: flex;
}

input[type="checkbox"]+label {
    &.onlylabel {
        margin-left: -13px;
        // padding-right: 25px;
        &:before {
            left: 0px;
        }
    }
}

input[type="checkbox"]+label,
.form-checkbox+label,
input[type="radio"]+label,
.form-radio+label {
    padding-left: 30px;
    margin-left: -13px;
    position: relative;
}

input[type="checkbox"],
.form-checkbox,
input[type="radio"],
.form-radio {
    width: 13px;
    height: 13px;
    opacity: 0;
    z-index: 1;
}

input[type="checkbox"],
.form-input {
    +label {
        &:before {
            content: "";
            background-color: $gray-200;
            font-size: 12px;
            transition: 0.3s ease;
            color: #fff;
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
        }
    }
    &:checked {
        +label {
            &:before {
                background-color: $primary;
                content: "\f00c";
                position: absolute;
                top: 0;
                left: 0;
                height: 20px;
                width: 20px;
                font-family: FontAwesome;
                font-size: 13px;
                text-align: center;
                line-height: 20px;
            }
            &.onlylabel {
                &:before {}
            }
        }
    }
}

input[type="checkbox"] {
    &.form-toggle {
        width: 0;
        height: 0;
        position: absolute;
        +label {
            display: inline-block;
            height: 16px;
            position: relative;
            width: 35px;
            border-radius: 35px;
            background-color: $gray-200;
            margin-left: 0;
            &:before {
                background-color: #fff;
                content: "";
                top: -2px;
                left: 0px;
                position: absolute;
                @include transition (0.3s ease);
                height: 20px;
                width: 20px;
                border-radius: 50%;
                box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
            }
        }
        &.sm {
            +label {
                height: 13px;
                width: 30px;
                padding-left: 25px;
                &:before {
                    height: 15px;
                    width: 15px;
                    top: -1px;
                }
            }
            &:checked {
                +label {
                    &.toggle:before {
                        top: -1px;
                    }
                }
            }
        }
    }
    &:checked {
        +label {
            &.toggle {
                &:before {
                    transform: translateX(15px);
                    background-color: $primary;
                    top: -2px;
                    content: "";
                }
            }
        }
    }
}

input[type="radio"],
.form-radio {
    width: 13px;
    height: 13px;
    opacity: 0;
    z-index: 1;
    +label {
        &:before {
            border: 2px solid #ccc;
            width: 20px;
            height: 20px;
        }
        &:before,
        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.08);
            color: rgba(0, 0, 0, 0.0125) !important;
            -webkit-transition: none !important;
            transition: none !important;
            text-align: center;
            line-height: 20px;
            font-size: 12px !important;
        }
    }
    &:checked {
        +label {
            &:after {
                position: absolute;
                top: 5px;
                left: 5px;
                background: rgba($success, 0.8);
                width: 10px;
                height: 10px;
                border-radius: 50%;
            }
        }
    }
}

.sfCheckbox-inline {
    display: flex;
    align-items: center;
    >.formvalue {
        padding-left: 20px;
