.sfFormwrapper {
    .sticky{
        .sfFieldset{
            margin-bottom:10px;
        }
    }
    .sfFieldset {
        display: block;
        margin-bottom: 20px;
        // &:not(:last-child) {
        //     margin-bottom: 20px;
        // }
        .formvalue {
            position: relative;
            >div:not(:last-child),
            >div:not(.sfCheckbox-toggle) {
                &:not(:last-child) {
                    margin-bottom: 12px;
                }
            }
            .sfCheckbox{
                margin-bottom:12px;
            }
            &.horizontal-list {
                display: flex;
                align-items: center;
                >div:not(:last-child) {
                    padding-right: 25px;
                }
                >div.sfCheckbox-toggle {
                    padding-right: 15px;
                    margin-bottom: 0;
                }
            }
        }
        .formkey,
        .formvalue {
            display: block;
            width: auto;
            margin-top: 5px;
            input[type="checkbox"],
            input[type="radio"] {
                width: 13px;
                height: 13px;
            }
        }
        &.sfRow {
            display: flex;
            flex-direction: row;
            align-items: center;
            .formKey,
            .formValue {
                margin: 0;
                &.sfCol-auto {
                    width: auto;
                }
            }
        }
    }
    .sfButtonwrapper {
        margin-top: 25px;
    }
   
    .select2-container--default{
        .select2-selection--multiple{
            min-height:38px;
            padding:0 8px;
           >ul{ li{
            
                &.select2-selection__choice{
                    cursor: pointer;
                    margin: 6px 5px 0 0 ;
                    padding: 4px 12px;
                    background: #bbb;
                    color: #fff;
                    border-radius: 25px;
                    font-size: 0.8rem;
                
                }
                &.select2-search {
                    position: relative;
                    padding-right: 30px!important;
                }
            }
           } 
        }
        &.select2-container--default.select2-container--focus .select2-selection--multiple{
            border: 1px solid lighten($primary, 32%) !important;
            background: lighten($primary, 42%);
        }
        }
}

