                        white-space: nowrap;
                        padding: 5px 10px;
                        border-radius: 2px;
                        margin-top: 10px;
                        position: absolute;
                        left: 50px;
                        bottom: 100%;
                        margin-bottom: 5px;
                        z-index: 1;
                        background: rgba(0, 0, 0, 0.45);
                        font-family: $base-font-family;
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
        >ul>li>.navlinks i+span {
            opacity: 0;
            visibility: hidden;
            @include respond-below(sm) {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    .side-nav-link {
        text-align: left;
    }
}

.sidebar_links li {
    &.active {
        background: rgba(white, 0.08);
    }
    position: relative;
    &.has-child {
        &:hover {
            >.side-nav-link {
                +.sub-menu {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }
    &:last-child {
        .sub-menu {
            top: auto;
            bottom: 0;
        }
    }
}

