    top: 50px;
    right: 0;
    background: #fff;
    box-shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15);
    padding: 20px;
    border-radius: 5px;
    max-width: 250px;
    input[type="text"] {
        @extend .sfFormcontrol;
        margin-bottom: 15px;
    }
}

.chart-date-filter {
    .chartfilter {
        @include button(5px 10px, 0.75rem, 3px, $font-weight-medium);
        border: 1px solid #969dc9;
        color: #969dc9;
        padding: 5px 10px;
        &:hover {
            border: 1px solid transparent;
            color: darken(#969dc9, 30%);
            background: lighten(#969dc9, 24%)
        }
    }
    .list-group {
        font-size: 0.9rem;
        max-width: 250px;
        right: 50px;
        .group-item {
            color: $gray-600;
        }
    }
}

.chartwithdata {
    flex-wrap: wrap;
    align-items: center;
    >span {
        font-size: 2.5em;
        font-family: Poppins;
        padding-right: 30px;
        flex: 1 0 30%;
        text-align: center;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        strong {
            font-weight: $font-weight-semibold;
        }
    }
    canvas {
        max-width: 220px;
    }
}

.charts .dropdown-link i {
    font-size: 0.85rem;
}

//customizelogo
.infos {
    font-size: 0.9rem;
    color: $gray-600;
    margin: 10px 0 0;
}

//massmail
.import-excel {
    height: 100px;
    width: 97px;
    margin: 0 auto;
    margin-bottom: 30px;
    &.upload {
        background: url(../../images/upload.png) no-repeat;
    }
    &.xls {
        background: url(../../images/xls.png) no-repeat;
    }
}

.import-modal {
    .modal-content {
        overflow: hidden;
        border-radius: 10px;
    }
    .modal-header {
        h5 {
            width: 100%;
            font-weight: $font-weight-regular;
            @include font-size($h3-font-size);
            text-align: center;
        }
        align-items:center;
    }
    .modal {
        &-header,
        &-body,
        &-footer {
            padding: 1.5rem;
        }
    }
}

//profile
.profile-img {
    width: 200px;
    height: 200px;
    overflow: hidden;
    + .uploads {
        .customfileUploder{
            position:absolute;
            bottom:auto;
        }
    }
}

.divColorView{
    border:1px solid $gray-900;
    width:32px;
    height:32px;
    display:inline-block;
    border-radius:3px;
}
.dashboardlinks{
    color:#fff;
    transition:0.3s ease;
    i{
    font-size:3.25rem;
    color:rgba(255,255,255,0.35);
    display:flex;
    align-items:center;
    }
    .blue{
    background-color: #2092ed;
    }
    .yellow{
        background-color: #ffc107;
    }
    .green{
        background-color: #28a745;
    }
    .card-body{
        padding:30px 25px;
        border-radius:8px;
    }
    .card-body{  
          box-shadow: 0 2rem 1.5rem -1.5rem rgba(33,37,41,0.15),0 0 1.5rem 0.5rem rgba(33,37,41,0.05) ;
        }
        &:hover{
            transform:translateY(-10px)
        }

}
.linkcontent{
    width:75%;
    margin-left:auto;
}