$max-value:4;
@for $i from 1 through $max-value {
    $value: $i *25;
    $multValue: $i * 25%;
    .h-#{$value} {
        height: $multValue !important;
    }
}

$max-value:20;
@for $i from 1 through $max-value {
    $value: $i *5;
    $multValue: $i * 5%;
    .w-#{$value} {
        width: $multValue !important;
    }
}
@include respond-above(sm){
    @for $i from 0 through $max-val {
        $value: $i *5;
        $multValue: $i * 5%;
        .sm-w-#{$value} {
            width: $multValue !important;
        }
    }
}
