.sfContainer {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    &-full,
    &-xl,
    &-lg,
    &-md,
    &-sm {
        width: 100%;
        padding: 0 15px;
        padding-left: 15px;
        padding-right: 15px;
        margin-left: auto;
        margin-right: auto;
    }
    @include respond-above(xs) {
        max-width: 540px;
        &-sm {
            max-width: 100%;
        }
    }
    @include respond-above(sm) {
        max-width: 720px;
        &-sm {
            max-width: 720px;
        }
        &-md,
        &-lg {
            max-width: 100%;
        }
    }
    @include respond-above(md) {
        max-width: 960px;
        &-sm,
        &-md {
            max-width: 960px;
        }
        &-lg {
            max-width: 100%;
        }
    }
    @include respond-above (xl) {
        max-width: 1160px;
        &-sm,
        &-md,
        &-lg {
            max-width: 1160px
        }
    }
}

.sfRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.sfCol-auto {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    width: auto;
    max-width: none;
}

.sfCol {
    -ms-flex-preferred-size: 0;
    flex-basis: 0;
    -ms-flex-positive: 1;
    flex-grow: 1;
    max-width: 100%;
}

.sfCol,
.sfCol-auto {
    padding-left: 15px;
    padding-right: 15px;
}

.sfCol-auto {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    width: auto !important;
    max-width: none;
}

.no-guts {
    margin-right: 0 !important;
    margin-left: 0 !important;
}

[class^=sfCol-],
.sfCol {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    position: relative;
}

[class*=sfCol-] {
    padding-left: 15px;
    padding-right: 15px;
}

.no-guts>[class^="sfCol-"],
.no-guts>[class*="sfCol-"],
.no-guts>.sfCol,
.no-guts>div {
    padding-left: 0;
    padding-right: 0;
}

@mixin make-container($gutter: $grid-gutter-width) {
    width: 100%;
    padding-right: $gutter / 2;
    padding-left: $gutter / 2;
    margin-right: auto;
    margin-left: auto;
}

@mixin make-row($gutter: $grid-gutter-width) {
    display: flex;
    flex-wrap: wrap;
    margin-right: -$gutter / 2;
    margin-left: -$gutter / 2;
}

@mixin make-col($size, $columns: $grid-columns) {
    flex: 0 0 percentage($size / $columns);
    // Add a `max-width` to ensure content within each column does not blow out
    // the width of the column. Applies to IE10+ and Firefox. Chrome and Safari
    // do not appear to require this.
    max-width: percentage($size / $columns);
    padding-left: 15px;
    padding-right: 15px;
}

@mixin make-col-ready($gutter: $grid-gutter-width) {
    width: 100%;
    position: relative;
    padding-right: $gutter / 2;
    padding-left: $gutter / 2;
}

@mixin make-col-offset($size, $total: $grid-columns) {
    $num: $size/$total;
    margin-left: if($num==0, 0, percentage($num));
}

.sfGrid{
    display:grid;
}