@mixin button($padding:5px 14px, $font-size:0.85rem, $border-radius:3px, $font-weight:$font-weight-semibold) {
    padding: $padding;
    height: auto;
    display: inline-block;
    border: 1px solid transparent;
    border-radius: $border-radius;
    font-family: $base-font-family;
    font-size: $font-size;
    line-height: 1.5;
    font-weight: $font-weight;
    margin-right: 0px;
    text-transform: capitalize;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
}

@mixin border-radius($radius) {
    -webkit-border-radius: $radius;
    border-radius: $radius;
}

@mixin button-bg-outline($bg) {
    background: none;
    border-color: $bg;
    border-width: 1px;
    border-style: solid;
    color: $bg;
    &:hover {
        background: $bg;
        color: #fff;
    }
}

@mixin button-bg($bg) {
    background: $bg;
    border-color: transparent;
    color: #fff;
    &:hover {
        background: darken($bg, 20%);
        color: #fff;
    }
}

@mixin font-size($size) {
    font-size: $size
}

//link only
@mixin button-link($color) {
    background: none;
    border: none;
    color: $color;
    &:hover {
        background: none !important;
        color: darken($color, 50%);
    }
}

@mixin transition($var) {
    transition: all $var ease;
    -webkit-transition: all $var ease;
    -moz-transition: all $var ease;
    -ms-transition: all $var ease;
}

@mixin border-radius($var) {
    border-radius: $var
}

.fade {
    @include transition($transition-fade);
    // &:not(.show) {
    //     opacity: 0;
    // }
}

.collapse {
    &:not(.show) {
        display: none;
    }
}

@mixin clearfix() {
    &::after {
        display: block;
        clear: both;
        content: "";
    }
}

@mixin float-left {
    float: left !important;
}

@mixin float-right {
    float: right !important;
}

@mixin float-none {
    float: none !important;
}

@mixin transition($args) {
    -webkit-transition: $args;
    -moz-transition: $args;
    -ms-transition: $args;
    -o-transition: $args;
    transition: $args;
}

@mixin color($color, $opacity) {
    color: rgba($color, $opacity);
}

@mixin bg-color-alpha($color, $opacity) {
    background: rgba($color, $opacity) !important;
}

@mixin color-alpha($color, $opacity) {
    color: rgba($color, $opacity) !important;
}

@mixin box-shadow($top, $left, $blur, $color:#000, $inset: false) {
    @if $inset {
        -webkit-box-shadow: inset $top $left $blur $color;
        -moz-box-shadow: inset $top $left $blur $color;
        box-shadow: inset $top $left $blur $color;
    }
    @else {
        -webkit-box-shadow: $top $left $blur $color;
        -moz-box-shadow: $top $left $blur $color;
        box-shadow: $top $left $blur $color;
    }
}

@mixin abs-pos ($top: auto, $right: auto, $bottom: auto, $left: auto) {
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
    position: absolute;
    top: 0;
}