$total:12;
$grid-gutter-width:30px;
$prefix:"sfCol-";
@for $i from 1 through 12 {
    .#{$prefix}#{$i} {
        flex: 0 0 (($i/$total)*100) * 1%;
        max-width: (($i/$total)*100) * 1%;
        // min-width: (($i/$total)*100) * 1%;
        // padding-left: ($grid-gutter-width / 2);
        // padding-right: ($grid-gutter-width / 2);
    }
    .offset-#{$prefix}#{$i} {
        @include make-col-offset($i, $total);
    }
}

