.sfDatagrid {
    border-radius: 8px;
    background: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.08);
    border: 1px solid black(0.1);
}

.dg-col-nodata {
    max-width: 450px;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 30px;

    h5 {
        color: $gray-600;
        font-weight: 300;

        &:before {
            background: url(../../images/nodata.png) no-repeat;
            height: 100%;
            width: 100%;
            display: block;
            content: "";
            padding-top: 73.33%;
            background-size: contain;
            text-align: center;
            margin: 0 auto;
            margin-bottom: 30px;
        }
    }
}

.dg-col-wp {
    display: flex;
    min-width: 100%;
    max-width: 100%;
    flex-wrap: wrap;

    >[class^=sfCol-] {
        display: flex;
    } 
    &:last-child{
        .action-open{
            top:auto;
            bottom:0
        }
    }
}

.btn {
    .dropdown-link {
        transition: none;

        &:hover {
            color: inherit
        }
    }
}

.dropdown-link {
    cursor: pointer;
    @include transition (0.2s ease);

    i {
        font-size: 0.7rem;
        margin-left: 15px;
    }

    &:hover {
        color: $gray-600;
    }
}

.link-dropdown {
    i {
        font-size: 0.85rem;
        margin-left: 8px;
    }
}

.dg-dropdown {
    color: $gray-600;

    >* {
        color: inherit;
        @include transition(0.4s ease);

        &:hover {
            color: $gray-900;
        }
    }
}

.dropdown-group {
    &.enable {
        .list-dropdown+.list-group {
            display: block;
            box-shadow: 0 0 35px 0 rgba(154, 161, 171, .15);
        }
    }
}

.dg-dropdown-list {
    display: none;
}

.list-dropdown+.list-group {
    display: none;
    min-width: 220px;
    max-width: 300px;
    position: absolute;
    z-index: 1;
    margin-top: 20px;
    max-height: 350px;
    overflow: auto;

    &.wrap-list-group {
        border-radius: 8px;
        overflow: hidden;
    }

    >ul {
        max-height: 300px;
        overflow: auto;
    }

    .clear-list {
        background: $gray-400;
        padding: 8px 5px;
        width: 100%;
        display: block;
        text-align: center;
        transition: 0.3s ease;
        color: #fff;
        cursor: pointer;

        &:hover {
            background: darken($gray-400, 20%);
        }
    }
}

.sfDatagrid-header {
    .dg-col-wp {
        padding: 20px 5px;
        align-items: center;
        border-bottom: 1px solid $gray-200;
    }

    .dg-search-key {}
}

.sfDatagrid-title {
    .dg-col-wp {
        background: $gray-200;
        padding: 10px 5px;
        font-weight: 500;
    }

    .dg-group-inline {
        font-size: 1rem;
        color: $gray-700;
    }
}

.sfDatagrid-body {
    .dg-col-wp {
        padding: 20px 5px;
        align-items: baseline;
        border: 1px solid transparent;
        cursor: pointer;

        &:not(:last-child) {
            border-bottom: 1px solid rgba($gray-900, 0.05);
        }

        &:nth-child(even) {
            background: rgba($primary, 0.05);
        }

        &:hover {
            background: rgba($primary, 0.085);
        }
     
    }
}

.dg-thumb {
    width: 90px; 
    height: 90px;
    display: block;
    overflow: hidden;
    border: 1px solid #e7e7e7;
    border-radius: 3px;
    margin: 0;

    img {
        width: 100%;
        height: 100%;
        -o-object-fit: contain;
        object-fit: contain;
        -o-object-position: center;
        object-position: center;
        padding: 8px;
        background: #fff;
    }
}

.dg-thumb {
    &.md {
        width: 80px;
        height: 80px;
    }

    &.md {
        width: 120px;
        height: 120px;
    }

    &.lg {
        height: 140px;
        width: 140px;
    }

    &.xl {
        height: 180px;
        width: 180px;
    }
}

.dg-title {
    font-size: $grid-display2-size;
    line-height: 1.25;
    font-weight: $font-weight-regular;
    color: $primary;
    margin-bottom: 10px;
}

.dg-group {
    flex: 1;
}

.dg-group-inline {
    vertical-align: top;
    font-size: $grid-display4-size;
    font-weight: 500;
    color: $gray-600;

    &.checkbox {
        padding-right: 0
    }

    >span {
        display: inline-block;
        margin-bottom: 8px;

        &.grd-value {
            font-weight: 400;
            padding-left: 10px;
        }
    }

    &:last-child {
        padding: 0;
    }
}

.dg-search-key,
.dg-search-filter {
    position: relative;
    width:100%;
    @include respond-above(lg) {
        &.m-width{
        min-width: 280px;
    }
    }

    // .sfFormcontrol {
    //     height: 35px;
    //     font-size: 0.85rem;
    //     padding: 0 35px 0 13px;
    // }

    button {
        &.search {
            position: absolute;
            top: 50%;
            right: 0;
            width: 35px;
            height: 35px;
            border-radius: 50%;
            @include transition(0.3s ease);
            transform: translateY(-50%);

            &:hover {
                background: $primary;
                color: #fff;
            }
        }
    }
}

.dg-group-inline {
    padding-right: 25px;
    display: inline-block;
}

.dg-sec {
    > {
        .dg-group-inline:last-child {
            padding-right: 0;
        }
    }

    &:last-child {
        span {
            margin-bottom: 0;
        }
    }
}

.sfPagination {
    display: flex;
    margin-top: 25px;

    @at-root .pagination {
        margin-left: auto;

        >a,
        >span {
            color: $gray-400;
            border: 1px solid transparent;
            font-weight: 500;
            font-size: 0.85rem;
            border-radius: 3px;
            width: 35px;
            height: 35px;
            line-height: 35px;
            text-align: center;
            display: inline-block;
            margin-right: 3px;
            @include transition(0.3s ease);

            &:hover {
                color: $primary;
            }

            &.current {
                background: $primary;
                color: #fff;
                pointer-events: none;

                &.prev,
                &.next {
                    background: none;
                    color: $gray-400
                }
            }

            &.prev {
                &:before {
                    content: "\f053";
                    font-family: FontAwesome;
                    font-weight: 900;
                }
            }

            &.next {
                &:before {
                    content: "\f054";
                    font-family: FontAwesome;
                    font-weight: 900;
                }
            }

            &.next,
            &.prev {
                background: none;
                color: $primary;
            }
        }
    }
}

//date picker
.ui-datepicker-multi,
.xdsoft_datetimepicker {
    display: flex;
    width: auto !important;
    max-width: none !important;

    .ui-datepicker-group { 
        display: inline-block;
    }
}

.ui-datepicker,
.xdsoft_datetimepicker { 
    background: #fff;
    width: 100%;
    max-width: 340px; 
    border: 1px solid rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    box-shadow: 0px 10px 16px 0px rgba(0, 0, 0, 0.05);
    border-collapse: collapse;
    z-index: 11 !important;
    &.z-top{
       z-index: 99991 !important
    }
    &.ui-widget-content {
        border: none
    }

    &-header {
        @include flexbox;
        @include align-items(center);
        padding: 10px;
    }

    &-title {
        flex: 1;
        text-align: center;
        font-weight: 500;
        color: $gray-900;
    }

    &-title {
        @include flexbox();
        @include justify-content(center);

        select {
            text-align-last: center;
            text-align: center;
            -ms-text-align-last: center;
            -moz-text-align-last: center;
            flex: 1;
            text-align: center;
            font-weight: $font-weight-medium;
            color: $gray-900;
            font-family: $base-font-family;
            border: none;
            font-family: $base-font-size;

            &.ui-datepicker-month {
                max-width: 70px;
            }

            &:focus {
                outline: none;
            }
        }
    }

    &-calendar {
        width: 100%;
    }

    &-prev,
    &-next {
        background: url(../../images/btn-calendar.png) no-repeat;
        display: inline-block;
        cursor: pointer;
        height: 12px;
        text-align: center;

        >.ui-icon {
            clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
        }
    }

    &-prev {
        background-position: 0 0;

        &:hover {
            background-position: 0 -12px;
        }
    }

    &-next {
        transform: rotate(0deg) scale(-1);
        order: 3;

        &:hover {
            background-position: 0 -12px;
        }
    }

    th {
        color: $gray-900;
        font-weight: 500;
        border: none;
        padding: 5px;
    }

    td {
        text-align: center;
        padding: 5px;
        border: none;

        a {
            color: $gray-900;
            font-size: 1rem;
            display: inline-block;
            width: 35px;
            height: 35px;
            line-height: 35px;
            border-radius: 50%;
            @include transition(0.3s);

            &.ui-state-hover,
            &.ui-state-active {
                background: $primary;
                color: #fff;
            }

            &.ui-state-active {
                box-shadow: 0 0 0px 5px rgba(77, 121, 246, 0.25);
            }
        }

        &.ui-state-disabled {
            color: $gray-300;
        }
    }
}

.xdsoft_monthpicker {
    flex: 1;
    text-align: center;
    font-weight: 500;
    color: $gray-900;
}

.xdsoft_calendar {


    table {
        thead {
            th {
                color: $gray-900;
                font-weight: 500;
                border: none;
                padding: 5px;
            }
        }
    }
}

.xdsoft_datetimepicker {
    td {
        text-align: center;
        padding: 5px;
        border: none;

        div {
            color: $gray-900;
            font-size: 1rem;
            display: inline-block;
            width: 35px;
            height: 35px;
            line-height: 35px;
            border-radius: 50%;
            cursor: pointer;
            @include transition(0.3s);

            &:hover,
            &.active {
                background: $primary;
                color: #fff;
            }

        }

        &.ui-state-disabled {
            color: $gray-300;
        }
    }
}

.xdsoft_datetimepicker {
    .xdsoft_timepicker {
        .xdsoft_time_box {
            height: 200px;
            overflow: hidden;
            border-bottom: 1px solid #ddd;

            >div>div {
                background: #f5f5f5;
                border-top: 1px solid #ddd;
                color: #666;
                font-size: 0.85rem;
                text-align: center;
                border-collapse: collapse;
                cursor: pointer;
                border-bottom-width: 0;
                padding: 6px 15px;

                &:hover {
                    background: #1fb6ff;
                    color: #fff;
                }
            }
        }

        .xdsoft_prev,
        .xdsoft_next {
            height: 8px;
            width: 12px;
            display: block;
            background: url(../../images/btn-calendar-2.png) no-repeat;

            &:hover {
                background-position: 0 0;
            }
        }

        .xdsoft_next {
            background-position: -12px 0;
            margin: 20px auto 20px auto;
            transform: rotate(180deg);
        }

        .xdsoft_prev {
            background-position: -12px 0;
            margin: 20px auto 20px auto;

        }
    }

    .xdsoft_monthpicker {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 10px 0;

        .xdsoft_prev,
        .xdsoft_next {
            width: 20px;
            min-width: 0;
            background: url(../../images/btn-calendar.png) no-repeat;
            display: inline-block;
            cursor: pointer;
            height: 12px;
            text-align: center;

            &:hover {
                background-position: 0 -12px;
            }
        }

        .xdsoft_next {
            transform: rotate(180deg);
        }

    }
}

.xdsoft_month{
    span {
    &:after {
        content: "";
        margin-top: 5px;
        background: url(../../images/btn-calendar-2.png) no-repeat;
        background-position: -12px 0;
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
        display: inline-block;
        width: 12px;
        height: 8px;
        margin-left: 14px;
        cursor: pointer;
    }
}
    &:hover {
        &::after {
            background-position: 0 0;
        }
    }
    .xdsoft_monthselect{
        position:absolute;
        margin-top: 40px;
        top: 0;
        background:#fff;
        border: 1px solid #ccc;
        width: 130px;
        text-align: left;
        
        cursor:pointer;
        .xdsoft_option{
            padding: 2px 5px;
            &:not(:last-child){
                margin-bottom:5px;
            }
            &:hover{
                background:$primary;
                color:#fff;
            }
        }
        }
}
